.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Modal {
  width: 600px;
  height: 400px;
  padding: 10px;
  background-color: honeydew;
  border-radius: 20px;
}

.divBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 15px;
}
.modalButton {
  width: 170px;
  height: 49px;
  border: none;
  background: #3a7999;
  color: #f2f2f2;
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  position: relative;
  box-sizing: border-box;
  transition: all 500ms ease;
}
.modalButton:hover {
  background: rgba(0, 0, 0, 0);
  color: #3a7999;
  box-shadow: inset 0 0 0 3px #3a7999;
}
.textDivision {
  margin-bottom: 10px;
  display: flex;
  justify-content: left;
  align-items: center;
  outline: 1px solid blue;
  width: 100%;
  height: 80%;
  background-image: url("data:image/jpeg;base64,");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}
.text {
  font-size: 2em;
  color: black;
  width: 50%;
  line-height: 1em;
  padding-left: 5px;
}
.numberTry {
  color: red;
  font-size: 2.2em;
}
.text span {
  color: red;
}
